<template>
    <div class="main">
        <h1>数据输入</h1>
        <h2>帮助</h2>
        <p>
            填写事项以及上传图片之后，点击完成进入结果页。按下键盘上的F11按键进入网页全屏，然后截图保存屏幕即可。
        </p>
        <h2>事项输入</h2>
        <div class="color-picker">
            <span>选择待办事项颜色</span>
            <el-color-picker
                size="small"
                v-model="color"
                show-alpha
            ></el-color-picker>
        </div>
        <div class="item" v-for="(item, index) in items" :key="index">
            <el-date-picker
                slot="prepend"
                placeholder="截止日期"
                v-model="items[index].deadline"
                value-format="yyyy/MM/dd"
                format="yyyy年MM月dd日"
            ></el-date-picker>
            <el-input placeholder="事项内容" v-model="items[index].title">
                <el-button slot="append" @click="items.splice(index, 1)">
                    <i class="el-icon-delete"></i>
                </el-button>
            </el-input>
        </div>
        <el-button round size="mini" class="add-btn" @click="addNewItem">
            <i class="el-icon-plus"></i>
        </el-button>
        <h2>图片选择</h2>
        <el-upload
            action="#"
            list-type="picture-card"
            :on-change="onChange"
            :show-file-list="false"
            :auto-upload="false"
            v-show="!image"
        >
            <i slot="default" class="el-icon-plus"></i>
        </el-upload>
        <div class="preview">
            <img v-if="image" :src="image" alt="" />
            <div class="mask" @click="image = ''">
                <i class="el-icon-delete"></i>
            </div>
        </div>
        <el-button type="primary" class="finish" @click="validate">
            完成
        </el-button>
    </div>
</template>

<script>
import { data } from "../store";
export default {
    data() {
        return {
            items: [
                {
                    deadline: "",
                    title: "",
                },
            ],
            color: "#000000",
            image: "",
        };
    },
    methods: {
        addNewItem() {
            this.items.push({
                deadline: "",
                title: "",
            });
        },
        onChange(e) {
            let reader = new FileReader();
            reader.readAsDataURL(e.raw);
            let that = this;
            reader.onload = function () {
                that.image = reader.result;
            };
        },
        validate() {
            if (!this.image) {
                this.$message({
                    type: "error",
                    message: "请上传背景图片",
                });
                return false;
            }
            if (this.items.length === 0) {
                this.$message({
                    type: "error",
                    message: "请填写至少一项事项",
                });
                return false;
            }
            data.image = this.image;
            data.items = this.items;
            data.color = this.color;
            this.$router.push({ name: "Result" });
        },
    },
    created() {
        if (data.image) this.image = data.image;
        if (data.items) this.items = data.items;
        if (data.color) this.color = data.color;
    },
};
</script>

