    box-sizing: border-box;
    padding: 16px 32px;
    max-width: 800px;
    min-height: 100vh;
    margin: 0 auto;
    box-shadow: 2px 2px 5px rgba($color: #000000, $alpha: 0.1);
    background-color: white;
}
