    border-left: 3px solid #ccc;
    padding: 8px 0 8px 8px;
}
.color-picker {
    display: flex;
    align-items: center;
    column-gap: 8px;
    margin-bottom: 16px;
}
.item {
    display: flex;
    column-gap: 8px;
    margin-bottom: 16px;
}
.add-btn {
    display: block;
    width: 80%;
    margin: 0 auto;
}
.preview {
    cursor: pointer;
    width: 100%;
    margin: 0 auto;
    position: relative;
    img {
        width: 100%;
    }
    .mask {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba($color: #000000, $alpha: 0.6);
        color: white;
        font-size: 32px;
        opacity: 0;
        transition: opacity ease 0.1s;
    }
    &:hover > .mask {
        opacity: 1;
    }
}
